.statistics {
  width: 60%;
  /* height: 300px; */
  /* height: 300px; */
  /* height: 100%; */
  /* height: 200px; */
  background-color: #fff;
  border-radius: 10px;
}
.statistics__header {
  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 10px;
}
.statistics__header--left {
  color: #121d3e;
  font-weight: 600;
  font-size: 14px;
}
.statistics__content {
  width: 100%;
  height: 250px;
}

/* **************************** MEDIA QUERY *********************************************** */

@media (max-width: 1200px) {
  .statistics__content {
    height: 200px;
  }
}
