.counter {
  width: 30%;
  /* height: 200px; */
  background-color: #fff;
  padding: 15px 20px;
  color: #030d2e;
  border-radius: 10px;
}

.counter__title {
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 25px;
  margin-top: 20px;
}

.counter__item {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  font-weight: 600;

  padding: 20px 0;
  border-bottom: 1px solid #cdcdcd;
}

.counter__item:first-child {
  padding-top: 0;
}
.counter__item:last-child {
  /* padding-bottom: 0; */
  border-bottom: none;
  padding-bottom: 0;
}

.counter__item--button {
  display: flex;
  align-items: center;
}
.counter__item--button button {
  width: 14px;
  height: 14px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  border-style: none;
  border: 1px solid #a7a7a7;
  border-radius: 3px;
  cursor: pointer;

  /* padding: 2px; */
}
.counter__item--button p {
  border: 1px solid #a7a7a7;
  padding: 3px 6px;
  border-radius: 5px;
  margin: 0 8px;
}

.counter__item--right--input {
  width: 70px;
  display: flex;
  align-items: center;
  padding: 2px 5px;
  border: 1px solid #a7a7a7;
  border-radius: 8px;
  /* margin-right: 5px; */
}
.counter__item--right--input input {
  width: 90%;
}
input[type="text"] {
  color: #030d2e;
  font-size: 14px;
  font-weight: 500;
  font-family: "Inter", sans-serif;
}

/* **************************** MEDIA QUERY *********************************************** */
/* -------------------------------------@media (max-width: 1200px)----------------------------- */
@media (max-width: 1200px) {
  .counter {
    padding: 10px;
    font-size: 10px;
  }
  .counter__title {
    font-size: 14px;
  }
  .counter__item {
    font-size: 12px;
  }
}

/* -------------------------------------@media (max-width: 768px)----------------------------- */
@media (max-width: 768px) {
}

/* -------------------------------------@media (max-width: 575px)----------------------------- */
@media (max-width: 575px) {
  .counter__title {
    margin: 15px 0;
  }
  .counter__item {
    font-size: 10px;
    padding: 10px 0;
  }
}

